import i18n from "../i18n";
import StateAttributeAccess from './StateAttributeAccess';
import ComponentStateHelper from './ComponentStateHelper';
import RenderingHelper from '../components/RenderingHelper';
import PathTranslationHelper from './PathTranslationHelper';
import UserDefPathHelper from './UserDefPathHelper';
import IndexPathHelper from "./IndexPathHelper";
import TreeUtils from "../components/CbaTree/TreeUtils";
import TableHelper from "../components/table/TableHelper";

export default class TranslationManager {

  constructor() {
    const { t } = i18n;
    this.t = t;

    this.setupLanguageValues('', '', '', false);
    this.excludedComponentTypes = this.initializeComponentTypesToExclude();
    this.renderAfterTranslationComponentTypes = this.initializeRenderAfterTranslationComponentTypes();
  }

  changeLanguage = (language, runtime) => {
    i18n.changeLanguage(language);

    if (this.currentLanguage !== language || this.currentLanguage === '') {
      this.translationLanguage = language;
      this.translate(runtime);
    }
    this.currentLanguage = language;
  };

  setupTranslation = (itemConfig) => {
    if (itemConfig.translations) {
      Object.keys(itemConfig.translations).forEach((lng) => {
        i18n.addResources(lng, 'translation', itemConfig.translations[lng].translation);
        if (itemConfig.translations[lng].defaultLanguage) {
          this.setupLanguageValues(lng, '', '', true);
        }
      });
    } else {
      this.setupLanguageValues('', '', '', false);
    }
  }

  setupLanguageValues(defaultLanguage, currentLanguage, translationLanguage, useTranslation) {
    this.defaultLanguage = defaultLanguage;
    this.currentLanguage = currentLanguage;
    this.translationLanguage = translationLanguage;
    this.useTranslation = useTranslation;

    if (useTranslation) {
      i18n.changeLanguage(this.defaultLanguage);
    }
  }

  getTranslatedTextWithFallback = (key, defaultValue) => {
    const translatedText = this.getTranslatedText(key);
    if (translatedText === null) return defaultValue;
    return translatedText;
  }

  getTranslatedText = (key) => {
    if (!this.useTranslation) return null;
    const exists = i18n.exists(`${key}_Text`);
    return exists ? i18n.t(`${key}_Text`) : null;
  }

  getTranslatedText4GivenLanguage = (key, language) => {
    if (!this.useTranslation) return null;
    const exists = i18n.exists(`${key}_Text`, {
      lng: language
    });
    return exists? i18n.t(`${key}_Text`, {
      lng: language
    }) : null;
  }

  getTranslatedMouseOverWithFallback = (key, defaultValue) => {
    const translatedMouseOver = this.getTranslatedMouseOver(key);
    if (translatedMouseOver === null) return defaultValue;
    return translatedMouseOver;
  }

  getTranslatedMouseOver = (key) => {
    if (!this.useTranslation) return null;
    const exists = i18n.exists(`${key}_MouseOver`);
    return exists ? i18n.t(`${key}_MouseOver`) : null;
  }

  getTranslatedImageWithFallback = (key, defaultValue) => {
    const translatedImage = this.getTranslatedImage(key);
    if (translatedImage === null) return defaultValue;
    return translatedImage;
  }

  getTranslatedImage = (key) => {
    if (!this.useTranslation) return null;
    const exists = i18n.exists(`${key}_Image`);
    return exists ? i18n.t(`${key}_Image`) : null;
  }

  addTranslationChangedListener = (instance) => {
    const { runtime, config } = instance.props;
    const { userDefinedId } = config;

    const translation = (this.getTranslatedText(userDefinedId));
    if (translation !== null) {
      runtime.eventEmitter.addListener('translationUpdate', this.onTranslationUpdate.bind(this, instance));
    }
  }

  onTranslationUpdate = (instance) => {
    const { runtime, path, config } = instance.props;
    const { userDefinedId } = config;
    const pathState = ComponentStateHelper.getState(instance);
    StateAttributeAccess.setTextValue(pathState, runtime.translationManager.getTranslatedText(userDefinedId));
    ComponentStateHelper.registerState(instance, pathState);
    RenderingHelper.triggerRenderingViaPath(path, runtime);
  }

  initializeComponentTypesToExclude = () => {
    const excludedComponentTypes = [];
    // managed by the table translation
    excludedComponentTypes.push('CbaTableCell');

    // not implemented yet
    excludedComponentTypes.push('CbaTree');
    excludedComponentTypes.push('CbaTreeChildArea');
    excludedComponentTypes.push('CbaTreeView');
    excludedComponentTypes.push('CbaTreeCell');
    excludedComponentTypes.push('CbaTreeHeader');
    excludedComponentTypes.push('CbaTreeRow');

    excludedComponentTypes.push('CbaRichTextField');

    return excludedComponentTypes;
  }

  initializeRenderAfterTranslationComponentTypes = () => {
    const renderAfterTranslationComponentTypes = [];
    renderAfterTranslationComponentTypes.push('CbaComboBox');
    renderAfterTranslationComponentTypes.push('CbaList');
    renderAfterTranslationComponentTypes.push('CbaTableCellEditor');
    return renderAfterTranslationComponentTypes;
  }

  translate = (runtime) => {
    try {
      const itemName = runtime.taskManager.getCurrentTestTaskItemNames().item;
      const itemInfo = runtime.taskManager.getItemInfo(itemName);
      const { itemConfiguration } = itemInfo;

      const pagesUdis = [];
      itemConfiguration.pages.forEach((page) => {
        pagesUdis.push(page.content.config.userDefinedId);
      })

      const componentsToRenderAfterTranslation = [];
      const containersPathList = ComponentStateHelper.findIndexPathsInCurrentTaskOfComponentWithType('CbaContainer', runtime);
      containersPathList.forEach((path) => {
        const containerAbsoluteUserDefId = PathTranslationHelper.getUserDefPathForIndexPath(path, runtime);
        const containerUserDefId = UserDefPathHelper.getLastUserDefIdFromPath(containerAbsoluteUserDefId);
        if (containerUserDefId !== undefined && pagesUdis.includes(containerUserDefId)) {
          const memberPaths = ComponentStateHelper.findIndexPathsOfDescendants(path, runtime);
          memberPaths.forEach((memberPath) => {
            const componentType = ComponentStateHelper.getComponentTypeForIndexPath(memberPath, runtime);
            const translationData = this.getTranslationData(memberPath, runtime);

            switch (componentType) {
              case 'CbaTable':
                this.translateTable(memberPath, runtime);
                break;
              // Image Map: references the userDefinedId in the RegionMap but the image is managed by the container  
              case 'CbaContainer': {
                const imageMapUserDefinedId = this.getImageMapUserDefinedId(memberPath, runtime);
                if (imageMapUserDefinedId !== undefined) {
                  translationData.image = runtime.translationManager.getTranslatedImage(imageMapUserDefinedId);
                  translationData.hasImageTranslation = (translationData.image !== null);
                }
                this.translateGenericComponent(memberPath, translationData, runtime);
                break;
              }
              default:
                if (!this.excludedComponentTypes.includes(componentType)) {
                  this.translateGenericComponent(memberPath, translationData, runtime);
                }
            }

            // specific rendering after translation
            const receiverComponent = runtime.componentDirectory.findComponent(memberPath);
            const renderComponent = (receiverComponent !== undefined);
            if (renderComponent && this.renderAfterTranslationComponentTypes.includes(componentType)) {
              componentsToRenderAfterTranslation.push(memberPath);
            }
          });
        }
      })

      // specific rendering after translation
      componentsToRenderAfterTranslation.forEach((memberPath) => {
        RenderingHelper.triggerRenderingViaPath(memberPath, runtime);
      })

    } catch (e) {
      console.error(e);
    }
  }

  translateGenericComponent = (memberPath, translationData, runtime) => {
    const memberAbsoluteUserDefPath = PathTranslationHelper.getUserDefPathForIndexPath(memberPath, runtime);
    const memberUserDefinedId = UserDefPathHelper.getLastUserDefIdFromPath(memberAbsoluteUserDefPath);
    const receiverComponent = runtime.componentDirectory.findComponent(memberPath);
    const renderComponent = (receiverComponent !== undefined);

    if (translationData.hasTextTranslation) {
      const safeToTranslate = this.shouldUpdateComponentText(memberPath, memberUserDefinedId, runtime);
      if (safeToTranslate) {
        ComponentStateHelper.updateStateAttribute(StateAttributeAccess.extractTextValue, StateAttributeAccess.setTextValue, translationData.text, memberPath, runtime, renderComponent);
        // update language in state
        ComponentStateHelper.updateStateAttribute(StateAttributeAccess.extractLanguageValue, StateAttributeAccess.setLanguageValue, this.translationLanguage, memberPath, runtime, false);
      }
    }

    if (translationData.hasMouseOverTranslation) {
      ComponentStateHelper.updateStateAttribute(StateAttributeAccess.extractMouseOverValue, StateAttributeAccess.setMouseOverValue, translationData.mouseOver, memberPath, runtime, renderComponent);
    }

    if (translationData.hasImageTranslation) {
      ComponentStateHelper.updateStateAttribute(StateAttributeAccess.extractDefaultImage, StateAttributeAccess.setDefaultImage, translationData.image, memberPath, runtime, renderComponent);
    }
  }

  getTranslationData = (indexPath, runtime) => {
    const absoluteUserDefPath = PathTranslationHelper.getUserDefPathForIndexPath(indexPath, runtime);
    const userDefinedId = UserDefPathHelper.getLastUserDefIdFromPath(absoluteUserDefPath);
    const translatedText = runtime.translationManager.getTranslatedText(userDefinedId);
    const translatedMouseOver = runtime.translationManager.getTranslatedMouseOver(userDefinedId);
    const translatedImage = runtime.translationManager.getTranslatedImage(userDefinedId);
    return {
      hasTextTranslation: (translatedText !== null),
      hasMouseOverTranslation: (translatedMouseOver !== null),
      hasImageTranslation: (translatedImage !== null),
      text: translatedText,
      mouseOver: translatedMouseOver,
      image: translatedImage
    }
  }

  retrieveComponentLanguage = (pathId, runtime) => {
    let componentLanguage = ComponentStateHelper.getStateAttributeByPathId(StateAttributeAccess.extractLanguageValue, pathId, runtime);
    if (componentLanguage === undefined) {
      componentLanguage = this.defaultLanguage;
    }
    return componentLanguage;
  }

  shouldUpdateComponentText = (pathId, userDefId, runtime) => {
    const componentType = ComponentStateHelper.getComponentTypeForIndexPath(pathId, runtime);
    // dynamic edit component (table cell)
    if (componentType === 'CbaSingleLineInputField' && userDefId === undefined) return true;

    const componentLanguage = this.retrieveComponentLanguage(pathId, runtime);
    const textFromState = ComponentStateHelper.getStateAttributeByPathId(StateAttributeAccess.extractTextValue, pathId, runtime);
    const referenceTextTranslation = this.getTranslatedText4GivenLanguage(userDefId, componentLanguage);
    if (textFromState === undefined) return true;
    return (this.normalizeString(textFromState) === this.normalizeString(referenceTextTranslation));
  }

  normalizeString = (str) => {
    if (str === undefined) return undefined;
    return str.replace(/\r\n|\r/g, '\n');
  }

  getImageMapUserDefinedId = (memberPath, runtime) => {
    const containerComponent = runtime.pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(memberPath));
    if (containerComponent !== undefined) {
      const { imageMapUserDefinedId } = containerComponent.config;
      return imageMapUserDefinedId;
    }
    return undefined;
  }

  translateTable = (tablePath, runtime) => {
    if (this.shouldUpdateTableTexts(tablePath, runtime)) {
      const tableTranslationData = this.getTranslationData(tablePath, runtime);
      this.translateGenericComponent(tablePath, tableTranslationData, runtime);

      const tableAbsoluteUserDefPath = PathTranslationHelper.getUserDefPathForIndexPath(tablePath, runtime);
      const tableData = ComponentStateHelper.getStateAttributeByUserDefPath(StateAttributeAccess.extractTableData, tableAbsoluteUserDefPath, runtime);
      const receiverComponent = runtime.componentDirectory.findComponent(tablePath);
      const renderedComponent = (receiverComponent !== undefined);
      let filteredTableCells = this.findRelevantTableCells(tablePath, runtime);
      // process first cell at the end, so that the focus is set to this one
      if (filteredTableCells.length > 0) {
        filteredTableCells = filteredTableCells.slice(1).concat(filteredTableCells[0]);
      }

      filteredTableCells.forEach((memberPath) => {
        // translate cell
        const cellTranslationData = this.getTranslationData(memberPath, runtime);
        this.translateGenericComponent(memberPath, cellTranslationData, runtime);

        // update table
        const { config } = runtime.pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(memberPath));
        const { row, column, readOnly } = config;

        const updatedValue = (cellTranslationData.text === null) ? '' : cellTranslationData.text;
        if (tableData !== undefined) {
          tableData[row][column] = updatedValue;
        }
        if (renderedComponent) {
          runtime.eventEmitter.emit(`${tablePath}-contentUpdate`, updatedValue, row, column, readOnly);
        }

        // update text edit widgets
        const editMemberPath = IndexPathHelper.appendIndexToPageSegment(memberPath, 0);
        const editComponentType = ComponentStateHelper.getComponentTypeForIndexPath(editMemberPath, runtime);
        if (editComponentType === 'CbaSingleLineInputField') {
          const formula = ComponentStateHelper.getStateAttributeByPathId(StateAttributeAccess.extractFormula, memberPath, runtime);
          const updateEditWidgetValue = (formula !== undefined && TableHelper.isFormula(formula)) ? formula : cellTranslationData.text;
          cellTranslationData.text = updateEditWidgetValue;
          cellTranslationData.hasTextTranslation = (updateEditWidgetValue !== undefined);
          this.translateGenericComponent(editMemberPath, cellTranslationData, runtime);
        }
      })

      if (tableData !== undefined) {
        ComponentStateHelper.updateStateAttribute(StateAttributeAccess.extractTableData, StateAttributeAccess.setTableData, tableData, tablePath, runtime, false);
      }
    }
  }

  shouldUpdateTableTexts = (memberPath, runtime) => {
    const { config } = runtime.pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(memberPath));
    if (!config.isSpreadsheet) return false;

    const filteredCells = this.findRelevantTableCells(memberPath, runtime);
    const shouldUpdate = filteredCells.every((cellPath) => {
      const absoluteUserDefId = PathTranslationHelper.getUserDefPathForIndexPath(cellPath, runtime);
      const componentLanguage = this.retrieveComponentLanguage(cellPath, runtime);
      const userDefId = UserDefPathHelper.getLastUserDefIdFromPath(absoluteUserDefId);
      let textFromState = ComponentStateHelper.getStateAttributeByPathId(StateAttributeAccess.extractTextValue, cellPath, runtime);
      const formula = ComponentStateHelper.getStateAttributeByPathId(StateAttributeAccess.extractFormula, cellPath, runtime);
      if (formula !== undefined && TableHelper.isFormula(formula)) textFromState = formula;
      if (textFromState === undefined) return true;
      const referenceTextTranslation = this.getTranslatedText4GivenLanguage(userDefId, componentLanguage);
      return (textFromState === referenceTextTranslation);
    });
    return shouldUpdate;
  }

  findRelevantTableCells = (tablePath, runtime) => {
    const memberPaths = ComponentStateHelper.findIndexPathsOfDescendants(tablePath, runtime);
    return memberPaths.filter((tableMemberPath) => {
      const componentType = ComponentStateHelper.getComponentTypeForIndexPath(tableMemberPath, runtime);
      if (componentType === 'CbaTableCell') {
        const cellConfig = runtime.pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(tableMemberPath)).config;
        const { row, column } = cellConfig;
        if (row > 0 && column > 0) return true;
      }
      return false;
    });
  }

  translateTree = (treeAbsoluteUserDefPath, runtime) => {
    const treeIndexPath = PathTranslationHelper.getIndexPathForUserDefPath(treeAbsoluteUserDefPath, runtime);
    const { treeNodes, treeColumns } = TreeUtils.getTreeDataByPath(treeIndexPath, runtime);

    TreeUtils.parseNodes(treeNodes, (node) => {
      node.cells = node.cells.map((cell, index) => {
        const translatedText = runtime.translationManager.getTranslatedText(node.userDefinedId);
        if (translatedText !== null) {
          cell.label = translatedText;
        }
        return cell;
      });
      const translatedMouseOver = runtime.translationManager.getTranslatedMouseOver(node.userDefinedId);
      if (translatedMouseOver !== null) {
        node.tooltip = translatedMouseOver;
      }
      return true;
    })

    treeColumns.map((column, index) => {
      const translatedText = runtime.translationManager.getTranslatedText(column.userDefinedId);
      if (translatedText !== null) {
        column.label = translatedText;
      }
      const translatedMouseOver = runtime.translationManager.getTranslatedMouseOver(column.userDefinedId);
      if (translatedMouseOver !== null) {
        column.mouseOver = translatedMouseOver;
      }
      return column;
    })

    TreeUtils.setTreeDataByPath(treeIndexPath, runtime, treeNodes, null, treeColumns, null);
    TreeUtils.emitTreeEvent(runtime.eventEmitter, treeIndexPath, runtime.replayStepMonitor);
  }

}
